import * as vscode from 'vscode';

//Todo: Hm ly text ti tt c cc v tr con tr
export const getTextAtSelections = (): string[] | null => {
    const editor = vscode.window.activeTextEditor;
    if (!editor) return null;
    const selections = editor.selections;
    // Ly text trong tng vng chn
    const texts = selections.map((selection) => editor.document.getText(selection));
    return texts;
};

//Todo: Hm chn text ti tt c cc v tr con tr
export const insertTextAtSelections = (mutils: string[]) => {
    const editor = vscode.window.activeTextEditor;
    if (!editor) {
        vscode.window.showErrorMessage('Khng c editor no ang hot ng!');
        return;
    }
    editor.edit((editBuilder) => {
        const selections = editor.selections;
        selections.forEach((selection, index) => {
            if (!selection.isEmpty) {
                editBuilder.replace(selection, mutils[index] ?? ''); // Thay th vng chn
            } else {
                editBuilder.insert(selection.start, mutils[index] ?? ''); // Chn text ti v tr con tr
            }
        });
    });
};
